import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Utilidades } from './utilidades';

@Injectable({
  providedIn: 'root'
})
export class CotizacionService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public cotizacionFinishEdit(): Observable<any>{
    return this.http.put(this.utilidades.endPoint + "/api/cotizacion/finish/edit", null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public sendReportCotizacion(): Observable<any>{
    return this.http.post(this.utilidades.endPoint + "/api/cotizacion/send/reportCotizacion", null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public deleteProductoCotizacion(productoCotizacionId: number): Observable<any>{
    return this.http.delete(this.utilidades.endPoint + "/api/cotizacion/delete/producto/" + productoCotizacionId, {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public deleteCotizacion(): Observable<any>{
    return this.http.delete(this.utilidades.endPoint + "/api/cotizacion/delete/cotizacion", {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public updateCantidad(cotizacionProductoId: number, cantidad: number): Observable<any>{
    return this.http.put(this.utilidades.endPoint + "/api/cotizacion/update/productoCantidad/" + cotizacionProductoId + "/" + cantidad, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public addProducto(productoId: number, cantidad: number): Observable<any>{
    return this.http.post(this.utilidades.endPoint + "/api/cotizacion/addProducto/" + productoId + "/" + cantidad, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getCotizacion(): Observable<any>{
    return this.http.get(this.utilidades.endPoint + "/api/cotizacion/get", {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public createCotizacion(): Observable<any>{
    return this.http.post(this.utilidades.endPoint + "/api/cotizacion/create", null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
