import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { CatalogoService } from './catalogo.service';
import { Utilidades } from './utilidades';

@Injectable({
  providedIn: 'root'
})
export class UsuarioService {

  private utilidades:Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public findUsersByNameOrAll(name: string, page: number, size: number): Observable<any>{
    return this.http.get(this.utilidades.endPoint + "/api/usuario/findByNameOrAll/" + name + "/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public updatePassword(newPassword: string, oldPassword: string): Observable<any>{
    return this.http.put(this.utilidades.endPoint + "/api/usuario/update/password/" + newPassword + "/" + oldPassword, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public actualizarCliente(userData: any): Observable<any>{
    return this.http.put(this.utilidades.endPoint + "/api/usuario/actualizar/cliente", userData, {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
         return throwError(error);
      })
    );
  }

  public login(correo:string, password:string): Observable<any>{
    return this.http.post(this.utilidades.endPoint + "/api/login", {username: correo, password: password}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public crearCliente(clienteDto: any): Observable<any>{
    return this.http.post(this.utilidades.endPoint + "/api/usuario/registrar/cliente", clienteDto).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public activarCuenta(token: string): Observable<any>{
    return this.http.post(this.utilidades.endPoint + "/verificacion/codigo/" + token, {}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public validarToken(token:string): Observable<any>{
    return this.http.post(this.utilidades.endPoint + "/verificacion/token/" + token, {}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
