import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { CatalogoService } from 'src/app/services/catalogo.service';
import { NotificationsService } from 'src/app/services/notifications.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';
import { Utilidades } from 'src/app/services/utilidades';
import * as StompJs from '@stomp/stompjs';
import * as SockJS from 'sockjs-client';
import { VentasService } from 'src/app/services/ventas.service';
import { CarShopComponent } from '../car-shop/car-shop.component';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.scss']
})
export class NotificationsComponent implements OnInit {

  public static notifications: any[] = [];
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public paginadorNotifications: any;
  private utilidades: Utilidades = new Utilidades();
  public data: any;
  private modal: NgbModalRef;
  public static clientStomp: StompJs.Client;
  public comprobanteSelect: string;
  public sizeNotifications: number;
  private productoSelect: any;
  private cantidadModal: NgbModalRef;
  public cantidadProducto: number;

  constructor(private notificationsServices: NotificationsService,
    private router: Router,
    private ngbModal: NgbModal,
    private ventaService: VentasService,
    private catalogoService: CatalogoService) { }

  ngOnInit(): void {
    this.productoSelect = null;
    this.cantidadProducto = 1;
    this.sizeNotifications = 0;
    NotificationsComponent.clientStomp = null;
    NotificationsComponent.notifications = [];
    if (this.utilidades.getUsuarioLogeado()) {
      this.findNotifications(1, 3);
      this.connectionSocketNotifications();
      this.subscribeSocketNotifications();
    }
  }

  public comprobanteIncorrecto(ventaId: number): void {
    swal.fire({
      title: '¿Estas seguro de cambiar el estado de la venta?',
      text: "",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si!',
      cancelButtonText: 'No!'
    }).then((result) => {
      if (result.isConfirmed) {
        LoaderComponent.status = true;
        this.ventaService.changeEstadoByVenta(ventaId, "EL RECIBO NO ES CORRECTO", null).subscribe(
          response => {
            this.ngOnInit();
            this.modal.close();
            swal.fire("¡Estado actualizado!", "", "success");
          },
          error => {
            this.mensajesComponent.mensajesError(error);
          }
        );
      }
    });
  }

  private subscribeSocketNotifications(): void {
    let _this = this;
    if (_this.utilidades.getUsuarioLogeado().roles[0].authority == "ROLE_ADMIN") {
      NotificationsComponent.clientStomp.onConnect = function () {
        NotificationsComponent.clientStomp.subscribe("/topic/notificationsAdmin", function (message) {
          _this.findNotifications(1, 3);
          const Toast = swal.mixin({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener('mouseenter', swal.stopTimer)
              toast.addEventListener('mouseleave', swal.resumeTimer)
            }
          });
          let response: any = JSON.parse(message.body);
          Toast.fire({
            icon: 'warning',
            title: response.titulo
          });
        });
      }
    } else {
      NotificationsComponent.clientStomp.onConnect = function () {
        NotificationsComponent.clientStomp.subscribe("/topic/notificationsCliente", function (message) {
          _this.findNotifications(1, 3);
          const Toast = swal.mixin({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener('mouseenter', swal.stopTimer)
              toast.addEventListener('mouseleave', swal.resumeTimer)
            }
          });
          let response: any = JSON.parse(message.body);
          Toast.fire({
            icon: 'warning',
            title: response.titulo
          });
        });
      }
    }
  }

  private connectionSocketNotifications(): void {
    if (NotificationsComponent.clientStomp) {
      NotificationsComponent.clientStomp.deactivate();
    }
    NotificationsComponent.clientStomp = null;
    NotificationsComponent.clientStomp = new StompJs.Client({
      brokerURL: "ws://" + this.utilidades.endPointSock + "/socketConnection?token=Bearer " + this.utilidades.getToken(),
      reconnectDelay: 5000,
      onDisconnect: () => {
        this.ngOnInit();
      }
    });
    if (typeof WebSocket !== "function") {
      NotificationsComponent.clientStomp.webSocketFactory = function () {
        return new SockJS("http://" + this.utilidades.endPointSock + "/socketConnection?token=Bearer " + this.utilidades.getToken());
      }
    }
    NotificationsComponent.clientStomp.activate();
  }

  public paginadorNotificationsInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    this.paginadorNotifications = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

  public findNotifications(page: number, contentSize: number): void {
    LoaderComponent.status = true;
    this.notificationsServices.getNotifications(page, contentSize).subscribe(
      response => {
        NotificationsComponent.notifications = response.content;
        this.sizeNotifications = response.totalElements;
        this.paginadorNotificationsInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
      }
    );
  }

  public deleteAllNotifications() {
    LoaderComponent.status = true;
    this.notificationsServices.deleteAllNotification().subscribe(
      response => {
        swal.fire("¡Notificaciones eliminadas!", "", "success");
        NotificationsComponent.notifications = [];
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
      }
    );
  }

  public getNotifications(): any {
    return NotificationsComponent.notifications;
  }

  public openComprobante(content: any, url: string): void {
    this.comprobanteSelect = url;
    this.ngbModal.open(content, { size: 'xl' });
  }

  public saveCarrito(): void {
    if (sessionStorage.getItem("carrito")) {
      sessionStorage.removeItem("carrito");
    }
    sessionStorage.setItem("carrito", JSON.stringify(CarShopComponent.carrito));
  }

  public agregarProducto(): void {
    if (this.utilidades.getUsuarioLogeado()) {
      if (this.productoSelect) {
        const resultado: any = CarShopComponent.carrito.find(producto => producto.productoSelect.id == this.productoSelect.id);
        let cantidadActual: number = this.cantidadProducto;
        if (resultado) {
          cantidadActual += resultado.cantidad;
        }
        if (cantidadActual <= this.productoSelect.cantidad && this.cantidadProducto > 0) {
          let productoJSON: any = {
            productoSelect: this.productoSelect,
            producto: this.data,
            cantidad: this.cantidadProducto
          };
          if (resultado) {
            resultado.cantidad += this.cantidadProducto;
          } else {
            CarShopComponent.carrito.push(productoJSON);
            this.saveCarrito();
          }
          swal.fire("¡Agregado!", "¡Producto agregado al carrito!", "success");
          this.cantidadProducto = 1;
          this.modal.close();
          this.cantidadModal.close();
        } else {
          if (resultado) {
            let cantidadActual: number = this.productoSelect.cantidad - resultado.cantidad;
            swal.fire("¡No tenemos existencias para esta cantidad, cantidad existente:" + cantidadActual + "!", "Si deseas puedes crear una cotización y enviarla a nosotros, para separar o negociar tu pedido.", "error");
          } else {
            swal.fire("¡No tenemos existencias para esta cantidad!", "", "error");
          }
          this.cantidadProducto = 1;
        }
      } else {
        swal.fire("¡Selecciona un producto!", "", "error");
      }
    } else {
      swal.fire("¡Inicia sesión!", "¡Para comprar un producto, necesitas iniciar sesión en el sistema!", "warning");
    }
  }

  public openCantidad(content: any, producto: any): void {
    if (producto.cantidad <= 0) {
      swal.fire("¡Agotado!", "", "info");
    } else {
      this.productoSelect = producto;
      this.cantidadModal = this.ngbModal.open(content, { size: "lg" });
    }
  }

  public openDetalles(detalleProduct: any, detalleVenta: any, idObj: string, idNotification: number): void {
    if (idObj != null) {
      LoaderComponent.status = true;
      this.notificationsServices.deleteNotificationByIdNotification(idNotification).subscribe(
        response => {
          NotificationsComponent.notifications = [];
          let data: string[] = idObj.split("-");
          if (data[0] == "P") {
            LoaderComponent.status = true;
            this.catalogoService.getByProductoId(parseInt(data[1])).subscribe(
              response => {
                this.data = response;
                this.modal = this.ngbModal.open(detalleProduct, { size: 'xl' });
                LoaderComponent.status = false;
              },
              error => {
                this.mensajesComponent.mensajesError(error);
              }
            );
          } else if (data[0] == "V") {
            LoaderComponent.status = true;
            this.ventaService.geVentaByVentaId(Number(data[1])).subscribe(
              response => {
                this.data = response;
                this.modal = this.ngbModal.open(detalleVenta, { size: 'xl' });
                LoaderComponent.status = false;
              },
              error => {
                this.mensajesComponent.mensajesError(error);
              }
            );
          }
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    }
  }

}
